.testimonials h2{text-align:center;margin-bottom:20px}.card-container{display:flex;flex-wrap:wrap;justify-content:center;padding-bottom:75px}.testimonial-Card{position:relative;flex-basis:30%;background-color:white;box-shadow:5px 5px 5px 5px #c9c9c9;display:flex;flex-direction:column;flex-shrink:0;width:80%;margin:3vh 2vw 3vh 2vw}.testimonial-Card p{margin:5px 20px 0px 20px}.testimonial-Card .quote{margin:10px}.testimonial-Card .right{margin-top:-5px;width:92%}.testimonial-Card .right svg{float:right}.testimonial-Card hr{background-color:#BC204E;height:2px}.testimonial-Card .testimonial-text-container{height:350px;display:flex;justify-content:center;align-items:center}.testimonial-Card .testimonial-User{display:flex;align-items:center;justify-content:center;height:5rem;margin:0px 10px 20px 20px}.testimonial-Card .testimonial-User img{flex:1 1;max-width:25%;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%}.testimonial-Card .testimonial-User span{flex:1 1;padding:0 10%;font-weight:bolder}@media only screen and (max-width: 900px){.testimonial-Card{flex-basis:45%}}@media only screen and (max-width: 768px){.testimonial-Card{flex-basis:90%}.testimonial-Card .testimonial-text-container{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}}

.enroll{background:#FAF5F6;padding:20px 10% 0 10%;text-align:center}.enroll .enroll-card-container{display:flex;flex-wrap:wrap;justify-content:space-between}.enroll .enroll-card-container .enroll-Card{position:relative;border:2px solid #BC204E;flex-basis:45%;margin:35px 0;padding:20px 10px}.enroll .enroll-card-container .enroll-Card h4{margin-bottom:30px}.enroll .enroll-card-container .enroll-Card .card-number{position:absolute;top:-35px;left:-35px;background-color:#FAF5F6;color:#e2e2e2;padding:15px;font-weight:bolder;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%}@media only screen and (max-width: 929px){.enroll .enroll-card-container{margin:0}.enroll .enroll-card-container .enroll-Card{flex-basis:100%}}@media only screen and (max-width: 500px){.enroll .enroll-card-container .enroll-Card .card-number{font-size:30px;padding:20px;top:-35px;left:-35px}}@media only screen and (max-width: 487px){.enroll .enroll-card-container .enroll-Card .card-number{padding:10px;top:-25px;left:-25px}}@media only screen and (min-width: 347px) and (max-width: 373px){.enroll .enroll-card-container .enroll-Card .card-number{padding:2vw 2vw}}

.catapult .text{display:flex;flex-direction:column;flex-shrink:0;margin-top:70px}.catapult .text span{margin:10px 0}.catapult .text button{margin-top:20px;width:300px}@media only screen and (max-width: 767px){.catapult{padding-top:10%;padding-bottom:10%}.catapult .text{display:flex;align-items:center;text-align:center;margin-top:20px}.catapult .image img{width:90%}}

.contactUsContent{padding-top:7rem;position:relative}.contactUsContent .intro{text-align:center}.contactUsContent .intro h2{font-weight:bolder}.contactUsContent .form-map-container{display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:2rem}.contactUsContent .contact-form-container{height:35rem;flex-basis:48%;border-radius:1rem;-webkit-border-radius:1rem;-moz-border-radius:1rem;-ms-border-radius:1rem;-o-border-radius:1rem;box-shadow:0 3px 5px 6px rgba(126,126,126,0.082)}.contactUsContent .map-container{flex-basis:48%;display:flex;flex-direction:column}.contact-form-container{padding:2rem}.contact-form-container .form-elements{height:100%;display:flex;flex-direction:column;justify-content:space-between}.contact-form-container .form-elements ::-webkit-input-placeholder{color:#969696}.contact-form-container .form-elements :-ms-input-placeholder{color:#969696}.contact-form-container .form-elements ::-ms-input-placeholder{color:#969696}.contact-form-container .form-elements ::placeholder{color:#969696}.contact-form-container .form-elements input[type=text],.contact-form-container .form-elements textarea{padding:5px;border:1px solid #da4d77;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px}.contact-form-container .form-elements input[type=text]:focus,.contact-form-container .form-elements textarea:focus{outline:none}.contact-form-container .form-elements button{width:100%}.map-container .map-text-container{display:flex;align-items:center;font-size:2rem;margin-bottom:0.5rem}.map-container .map-text-container svg{padding-right:10px}.map-container .map-text-container span{font-size:1rem}@media only screen and (max-width: 767px){.contactUsContent .contact-form-container,.contactUsContent .map-container{flex-basis:100%;margin-bottom:2rem}}

.textBody ul{margin-left:1.1rem}.textBody ul li{list-style-type:disc;margin-bottom:1rem}

* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.hide {
    display: none;
}
